  grid-template-areas:
    'header'
    'img'
    'actions'
    'likes'
    'caption'
    'comments'
    'time'
    'input';
}
.post-content.is-expanded {
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    'img header'
    'img comments'
    'img actions'
    'img likes'
    'img time'
    'img input';
  max-width: 1200px;
  max-height: 600px;
}

section.post-header {
