.like-icon {
  fill: white;
  stroke-width: 3px;
  stroke: #262626;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.like-icon:hover {
  stroke: #8e8e8e;
}
.like-icon.is-liked {
  fill: #ed4956;
  stroke-width: 0px;
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-animation: scale-out 0.2s ease-in;
          animation: scale-out 0.2s ease-in;
}
@-webkit-keyframes scale-out {
  0% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes scale-out {
  0% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
